.product-count {
  display: none;

  form {
    display: flex;

    border-radius: 4px;

    background-color: $color-mantis;
  }

  .custom-input {
    //width: calc(100% - 2 * 24px);
    width: calc(100% - 2 * 32px);

    input {
      width: 100%;
      border: none;

      text-align: center;

      //font-size: 16px;
      //line-height: 24px;
      font-size: 24px;
      line-height: 36px;
      font-weight: 400;
      color: $color-default-white;

      background-color: $color-transparent;
    }
  }
}

.product-count.is-showed {
  display: block;
}

.product-count--middle-size {
  .btn {
    padding: 7px;

    font-size: 16px;
    line-height: 24px;

    svg {
      width: 24px;
      height: 24px;
    }
  }

  .custom-input {
    width: calc(100% - 2 * 24px);

    input {
      font-size: 16px;
      line-height: 24px;
    }
  }
}
